.contactContent {
  overflow: hidden;
  position: relative;
  height: 95vh;
  min-height: 650px;
  background: #726158;
  border-radius: $borderRadius;
  border: none;
  border-top: 1.5px solid rgb(105, 116, 119);
  border-bottom: 1.5px solid rgb(16, 16, 16);
  border-right: 1.5px solid rgb(70, 76, 78);
  border-left: 1.5px solid rgb(32, 32, 32);
  box-shadow: $shadow;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 40% 40% 20%;
  grid-template-areas:
    "1"
    "2"
    "3";

  .header {
    grid-area: 1;
    width: 100%;
    position: relative;
    background: linear-gradient(115deg, $color-1 0%, $black 100%);

    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      background: url(../../media/map.jpg) no-repeat center top/cover;
      filter: grayscale(100%);
    }
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 30px;
      background: url(../../media/shape.svg) no-repeat center / cover;
    }
  }
  .contactBox {
    position: relative;
    top: -200px;
    grid-area: 2;
    background: linear-gradient(115deg, $color-5 0%, $color-2 100%);
    box-shadow: $shadow;
    border: none;
    border-top: 1.5px solid rgb(105 116 119);
    border-bottom: 1.5px solid rgb(16 16 16);
    border-right: 1.5px solid rgb(70 76 78);
    border-left: 1.5px solid rgb(32 32 32);
    margin: 0 auto;
    padding: 1rem 3rem;
    z-index: 2;
    border-radius: $borderRadius;
    width: 500px;

    min-height: 300px;

    h1 {
      text-align: center;
      margin-top: 0.3rem;
      position: relative;
      color: $color-7;
      text-shadow: 0px 2px 2px #000000, 1px 2px 2px $black;

      &:after {
        content: "";
        height: 1px;
        width: 50%;
        background: $color-1;
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    ul {
      height: 72%;
      width: inherit;
      display: grid;
      margin-top: 3.5rem;

      li {
        position: relative;
      }

      i {
        font-size: 1.4rem;
        position: absolute;
        transform: translateY(2px);
        color: $color-2;
        text-shadow: 0px 2px 2px #000000, 1px 2px 2px $black;
      }
      span {
        font-size: 1.4rem;
        color: $color-1;
        text-shadow: 0px 2px 2px #000000, 1px 2px 2px $black;
        position: absolute;
        transform: translateX(52px);
        background: transparent;
        border: none;
        outline: none;
        width: 350px;
        transition: 0.2s ease;
      }
      .clickInput:hover {
        transform: translateX(56px);
        cursor: pointer;
      }
    }
  }
  .socialNetwork {
    grid-area: 3;
    @include verticalCenter;

    ul {
      position: relative;
      top: -100px;
      display: flex;
      text-align: center;
      width: 100%;
      justify-content: space-around;

      a {
        width: 20%;
        color: $color-7;
        text-shadow: 0px 2px 2px #000000, 1px 2px 2px $black;
        background: linear-gradient(115deg, $color-5 0%, $color-2 100%);
        border: none;
        border-top: 1.5px solid rgb(105 116 119);
        border-bottom: 1.5px solid rgb(16 16 16);
        border-right: 1.5px solid rgb(70 76 78);
        border-left: 1.5px solid rgb(32 32 32);
        border-radius: $borderRadius;
        display: inline-block;
        padding: 0.7rem 0;
        box-shadow: $shadow;
        transform: scaleY(0);
        transform-origin: center;
        transition: color 0.3s;

        &:before {
          background: linear-gradient(162.5deg, $color-2 0%, $color-5 100%);
          border-radius: 10px;
          position: absolute;
          content: "";
          top: 0px;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: -1;
          transition: opacity 0.3s;
          opacity: 0;
        }

        &:hover {
          color: $color-8;
          text-shadow: 0px 2px 2px #000000, 1px 2px 2px $black;
        }

        &:hover::before {
          opacity: 1;
          z-index: 1;
        }
        h4 {
          position: relative;
          z-index: 2;
          font-size: 1.5rem;
        }
        i {
          position: relative;
          z-index: 2;
          font-size: 2rem;
          margin-top: 0.3rem;
        }
      }
      //ANIMATION
      @mixin networkAnim($timeout) {
        animation: pop 1s forwards;
        animation-delay: $timeout;
      }
      @keyframes pop {
        to {
          transform: scaleY(1);
        }
      }
      a:nth-child(1) {
        @include networkAnim(0.4s);
      }
      a:nth-child(2) {
        @include networkAnim(0.8s);
      }
      a:nth-child(3) {
        @include networkAnim(1.2s);
      }
      a:nth-child(4) {
        @include networkAnim(1.6s);
      }
    }
  }
}
@media screen and (max-width: 650px) {
  .contactContent {
    h1 {
      font-size: 1.4rem;
    }
    span {
      font-size: 0.9rem !important;
    }
    h4 {
      font-size: 0.9rem;
    }
  }
}
@media screen and (max-width: 520px) {
  .contactContent {
    .contactBox {
      width: 95% !important;
    }

    .socialNetwork {
      h4 {
        display: none;
      }
    }
  }
}
